import React from 'react';
import './App.css';

class Attack extends React.Component {
	constructor() {
	    super();
	    this.state = {attack: "Main",sharpShooter:true, choosingBM: false, checkingIfHit: false, damage: 0};
	    this.p = 3;
	    this.dex = 3;
	    this.cb = 3;
	    this.s = -5;
	    this.cbDmg = 10;
	    this.archery = 2;
	    this.d20Roll = 0;
  	}

  	onAttack = () => {
  		
  		this.setState({checkingIfHit:true, damage: this.state.attack=="Main"?0:this.state.damage});
  		this.d20Roll = this.rollD(20);
  	}


  	skipBM = skip => {
  		let nextAttack = this.state.attack == "Main" ? "Bonus" : this.state.attack == "Bonus" ? "Second" : "Main";
  		this.setState({attack: nextAttack, choosingBM:false, damage: this.state.damage+(skip?0:this.rollD(8))});
  	}

  	hit = didHit => {
  		if(didHit){
  			let dmg = this.rollD(6) + this.dex + this.cbDmg + (this.state.sharpShooter?10:0);
  			this.setState({choosingBM:true, checkingIfHit:false, damage: this.state.damage + dmg});
  		}else {
	  		let nextAttack = this.state.attack == "Main" ? "Bonus" : this.state.attack == "Bonus" ? "Second" : "Main";
	  		this.setState({attack: nextAttack, choosingBM:false, checkingIfHit:false});
  		}
  	}


  	rollD = eyes => Math.floor(Math.random() * (eyes-1)) + 1

    render() {
    	const {attack, damage, choosingBM, checkingIfHit} = this.state;
    	const bonusToHit = this.archery+this.cb+(this.state.sharpShooter?-5:0)+(attack == "Main" || attack == "Second" ? this.dex+this.p:0)
	    return (
	    	<div id="attack">
	    	<div id="innerAttack">
	    		<h2>{attack} Action</h2>

	    		{choosingBM ?
	    			<div>
		    		 	<p>Attack Roll: {this.d20Roll} + {bonusToHit} = {this.d20Roll + bonusToHit}</p>
		    		 	<button onClick={() => this.skipBM(false)}> Add Battle Master (d8) </button>
		    		 	<button onClick={() => this.skipBM(true)}> Skip </button>
		    		 </div>
	    		 :
	    		 checkingIfHit ?
	    		 	<div>
		    		 	<p>Attack Roll: {this.d20Roll} + {bonusToHit} = {this.d20Roll + bonusToHit}</p>
		    		 	<button onClick={() => this.hit(true)}> Hit </button>
		    		 	<button onClick={() => this.hit(false)}> Miss </button>
		    		 </div>
	    		 :
	    		 	<div>
		    			<input className="inline" type="checkbox" checked={this.state.sharpShooter} onClick={()=>this.setState({sharpShooter:!this.state.sharpShooter})} /> <p className="inline"> Sharp Shooter </p> 
			    		<p>Attack Roll: d20 + archery + cb {attack == "Main" || attack == "Second" ? "+ dex + p ":""}{this.state.sharpShooter?"- s":""}= d20 + {bonusToHit.toString()}</p>  
			    		<button onClick={()=>this.onAttack()}>Attack</button>
		    		</div>
	    		}
	    		



	    		<p>Current Damage: {damage}</p>
	    	</div>
	    	</div>
	    );
    }

}

export default Attack;